.navbar {
	width: 100%;
	position: absolute;
	top: 3.7vh;
	z-index: 10;
	color: white;
	// transform: translateY(-100%)
	// opacity: 0
	transition: transform 1s cubic-bezier(0.39, 0.575, 0.565, 1), opacity 1s cubic-bezier(0.39, 0.575, 0.565, 1);
	.nav-container {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&.loaded {
		transform: translateY(0px);
		opacity: 1; }


	nav {
		width: 50%;
		ul {
			width: 100%;
			display: flex;
			justify-content: space-between;
			li {
				cursor: pointer;
				transition: color .4s ease;
				a {
					transition: color .4s ease;
					&:hover {
						color: $accent-color; } } } } } }

.burger {
	display: none; }

.logo-img {
	&.mob {
		display: none; } }

