.dropdown-content {
	position: absolute;
	padding: 25px 18px;
	background: #292B33;
	color: white;
	display: none;
	// margin-top: 14px
	opacity: 0;
	pointer-events: none;
	transition: opacity .5s ease;
	a {
		display: block;
		margin-top: 25px;
		&:nth-child(1) {
			margin-top: 0px; } } }

.nav-mobile-header {
	display: none; }
